.packs-table{
  color: #F8EEDE;
    font-size: 1.2rem;
    text-transform: uppercase;
}

.packs-table{
  border-collapse: separate;
  border-spacing: 0;
  td, th{
    min-width: 100px;
    text-align: center;
    padding: 10px;
  }
  tr:nth-child(odd){
    background-color: $light-gray;
  }
  tr:nth-child(even){
    background-color: $lighter-gray;
  }
  tr:nth-child(0), tr:last-child{
    background-color: $dark-black;
  }
}

.spreadsheet{
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.MuiCheckbox-colorPrimary svg {
  color: $white!important;
}

.MuiFormControlLabel-label{
  font-size: 1.2rem!important;
  font-family: $base-font-family!important;
  text-transform: uppercase;
  font-weight: 100;
  color: $beige;
}

.smallPacks{
  width: 20px;
  margin-right: 10px;
}

.packName{
  display: flex;
    flex-direction: column;
    justify-content: center;
}

.nameCell{
  display: flex;
}

.displayName{
  color: $beige;
  text-transform: uppercase;
}

.cloudArea{
  position: absolute;
}


.graphic{
  width: 60vw;
  height: auto;
  margin: 1vw;
}

.graphic-wrapper{
  width: 100%;
  display: flex;
  justify-content: center;
}

.loader-hidden {
  display: none;
}
.loader-active {
  display: block;
}

.loader{
  transform: scale(3);
  transform-origin: center;
  bottom: 50%;
  position: absolute;
}

label:not(.MuiFormControlLabel-root){
  position: absolute;
  top: -10px;
  left: 10px;
  z-index: 1;
  background: $gray-blue;
  padding: 0px 5px;
}

label:not(.MuiFormControlLabel-root)::before{
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  width: 100%;
  height: 100%;
  border:1px solid $lighter-gray;
}

#rarity{
  height: 12rem;
}

.token img{
  width: 175px;
}

#tokens{
  display: flex;
    flex-wrap: wrap;
    gap: 28px;
    justify-content: center;
}